import React, { useEffect, useState } from "react";
import "./teachers.scss";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import axios from "axios";

const Teachers = () => {
  const [data, setData] = useState([]);

  useEffect(() => {
    axios
      .get("https://selfschool.uz/api/teachers/")
      .then((res) => {
        setData(res.data.results);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  return (
    <div id="Ustozlar" className="teachers">
      <div className="container">
        <h1>Bizning super jamoamiz</h1>
        <div className="cards">
          {data?.map((el) => {
            return (
              <div className="cardT col-lg-3 col-md-4 col-sm-6" id={el.id} >
                <img src={el.image} alt="Teacher_img" />
                <div className="textsT">
                  <ArrowBackIcon />
                  <div className="infoo">
                    <h3>{el.about}</h3>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default Teachers;
